import React from "react"
import Tortas, { PieProps } from "../../../Utiles/Charts/Torta";
export interface rank {
    cantidad: number,
    estatus: number
}
export default class TortasMaster extends React.Component<{ lugar: string }, PieProps>{
    state = { title: '', values: [] };
    estados = ['CANCELADO', 'PENDIENTE', 'PROCESADA'];
    api = 'Cotizacion';
    title = '';
    render() {
        const { title, values } = this.state;
        return <div className={'center ' + this.props.lugar}>
            <div style={{ textAlign: 'center' }}>
                <Tortas title={title} values={values} />
                <h5>{this.title}</h5>
            </div>
        </div>
    }
    componentDidMount() {
        this.send()
    }
    async send() {
        let date = new Date();
        let a√±o = date.getFullYear(),
            mes = date.getMonth() + 1;
        try {
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/' + this.api + '/torta/mes/' + a√±o + '/' + mes, { headers: { 'Authorization': 'Bearer ' + token } });
            let datos: rank[] = await response.json();
            this.setState({
                values: this.estados.map((s, i) => {
                    const d = datos.find(d => d.estatus === i);
                    return [s, d ? d.cantidad : 0]
                })
            });
        } catch (error) {
            console.log(error);
            this.setState({ values: [] });
        }
    }
}