import React from "react"
import Area from "../../Utiles/Charts/Area";
export default class Ventas extends React.Component<{}, { dolares: number[][] }>{
    constructor(p: any) {
        super(p);
        this.state = { dolares: [] };
    }
    render() {
        return <div className="ventas">
            <Area label={['DIA', 'VENTAS']} values={this.get()} title="VENTAS DEL MES" hAxisTitle="DIA DEL MES" />
        </div>
    }
    get() {
        let d = this.state.dolares.sort((a, b) => a[0] - b[0]).map(d => [d[0] + '', d[1]]);
        console.log(d);

        return this.state.dolares.sort((a, b) => a[0] - b[0]).map(d => [d[0] + '', d[1]]);
    }
    async send() {
        let date = new Date();
        let a√±o = date.getFullYear(),
            mes = date.getMonth() + 1;
        try {
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Factura/linea/mes/'+ a√±o + '/' + mes, { headers: { 'Authorization': 'Bearer ' + token } });
            let dolares = await response.json();
            this.setState({ dolares });
        } catch (error) {
            console.log(error);
            this.setState({ dolares: [] });
        }
    }
    componentDidMount() { this.send() }
}